@tailwind base;
@tailwind components;
@tailwind utilities;
/*============ Header ================ */
.header {
  height: 90vh;
  @apply relative mb-40;
}

.bg-backdrop {
  height: 100vh;
  width: 100%;
  background: url("/src/img/bg-top-left.png") top left no-repeat, url("/src/img/bg-top-right.png") top right no-repeat;
  position: absolute;
  inset: 0;
  z-index: -1;
}
.bg-backdrop::after {
  content: "";
  position: absolute;
  inset: 0;
  background: var(--backdrop);
  backdrop-filter: blur(var(--bg-blur));
}

.nav {
  @apply container mx-auto p-8;
}
.nav-list {
  @apply flex justify-between items-center
		 text-center gap-8;
}
.nav-item .nav-link {
  @apply uppercase;
}
.nav-item .nav-link.nav-logo {
  @apply text-2xl capitalize text-orange-red;
}

.companies {
  @apply justify-self-center flex gap-8 text-center
	 col-span-12 place-items-center row-start-6 row-end-6;
}

.company-logo {
  @apply h-8;
}

.hero {
  @apply container mx-auto h-full grid
	 		grid-cols-12 grid-rows-6;
}
.hero-title {
  @apply col-start-1 col-span-5
		 		row-start-2 row-end-4
		 		mx-8 w-4/5 text-left;
}
.hero-heading {
  @apply text-6xl;
}
.hero-subtitle {
  @apply justify-self-center self-center transform -rotate-90
                col-start-10 col-end-11 row-start-4 row-end-6 
				text-black opacity-10 text-3xl w-full font-bold;
}
.hero-cta {
  @apply justify-self-center self-center w-full grid place-items-center
			   row-start-4 row-end-5 col-start-1 col-end-8 z-10 shadow-emerald-50;
}
.hero-avatars {
  @apply flex items-center
		 		col-start-1 col-end-6
		 		row-start-5 row-end-6 -mt-24;
}
.hero-avatars__list {
  @apply flex -space-x-2;
}
.hero-avatar__item {
  @apply grid place-items-center text-center;
}
.hero-avatar__item:nth-child(1) {
  z-index: 5;
}
.hero-avatar__item:nth-child(2) {
  z-index: 6;
}
.hero-avatar__item:nth-child(3) {
  z-index: 7;
}
.hero-avatar__item:nth-child(4) {
  z-index: 8;
}
.hero-avatar__item:nth-child(5) {
  z-index: 9;
}
.hero-avatar__item:nth-child(6) {
  z-index: 10;
}
.hero-avatar__item:nth-child(7) {
  @apply w-9 h-9;
  z-index: 11;
}
.hero-avatar__others {
  @apply w-full h-full rounded-full grid place-items-center 
			text-center text-xs text-white border-2 border-white bg-accent-green;
}
.hero-tagline {
  @apply m-2 p-2;
}
.hero-img--1 {
  @apply col-start-6 row-start-1 col-span-4 row-end-5;
}
.hero-img--2 {
  @apply col-start-6 row-start-1 col-span-4 row-end-5 transform translate-x-6 translate-y-4;
}
.hero-img--3 {
  @apply row-start-2 row-end-3 col-start-9 col-span-2 z-20 transform -translate-y-40;
}
.hero-img--4 {
  @apply justify-self-center col-start-10 col-end-12 row-start-3 row-end-4 z-50 transform -translate-x-4;
}
.hero-img--5 {
  @apply col-start-12 col-end-12 row-start-2 row-end-3;
}

.services {
  height: 90vh;
  @apply flex gap-20 my-40 items-center;
}
.services__col-left {
  @apply grow h-full w-1/2 grid grid-cols-6 grid-rows-6;
}
.services__col-right {
  @apply grow w-1/2;
}
.services__btns {
  @apply flex items-center gap-10 mt-8;
}
.services__border-curved {
  @apply justify-self-center col-start-1 col-span-6 row-start-1 row-span-5 z-10;
}
.services__background-circle {
  @apply row-start-1 row-span-4 col-start-1 col-span-4 transform -translate-x-48;
}
.services__carousel-image {
  @apply justify-self-center col-start-1 row-start-1 col-span-6 row-span-5 p-2 z-20;
}
.services__carousel-dots {
  @apply transform -translate-y-8 flex gap-2 row-start-6 row-end-6 justify-self-center self-center col-span-6;
}
.services__carousel-dot {
  @apply w-2.5 h-2.5 bg-gray-200 rounded-sm;
}
.services__carousel-dot--active {
  @apply bg-orange-red;
}
.services__calendar-alarm {
  @apply z-30 col-start-5 col-end-7 row-start-5 justify-self-center self-center relative;
}
.services__alarm {
  @apply absolute -top-4 -left-4 shadow-lg rounded-full;
}
.services__calendar {
  @apply shadow-2xl;
}

.full-yellow-circle {
  @apply w-96 h-96 bg-yellow-light rounded-full row-start-2 row-span-4 col-start-1 col-span-4 hidden;
}

.visions {
  @apply flex gap-20 my-40 items-center;
}
.visions__col-right {
  @apply h-full w-1/2 grid grid-cols-6 grid-rows-6 place-items-center;
}
.visions__col-left {
  @apply w-1/2;
}
.visions__list {
  @apply grid grid-cols-4 grid-rows-2 gap-x-8 gap-y-10;
}

.vision__title {
  @apply flex gap-4 items-center;
}
.vision-1 {
  @apply col-start-1 col-span-2;
}
.vision-2 {
  @apply col-start-3 col-span-2;
}
.vision-3 {
  @apply col-span-4 row-start-2 row-end-3;
}
.vision-img--1 {
  @apply col-start-1 col-span-4 row-start-4 row-span-3 transform -translate-x-10 z-10 justify-self-end;
}
.vision-img--2 {
  @apply col-start-2 col-span-3 row-start-1 row-span-3 z-20 self-start justify-self-end;
}
.vision-img--3 {
  @apply col-start-4 col-span-3 row-start-3 row-span-3 z-30 justify-self-end self-start transform -translate-x-8;
}
.vision-img--4 {
  @apply col-start-5 row-start-1 z-40 self-start justify-self-start transform -translate-y-16;
}

.rounded-left-box {
  width: 27.8125rem;
  height: 21.875rem;
  background: #fff3c8;
  border-top-left-radius: 9.0625rem;
  @apply col-start-1 col-span-full row-start-2 row-span-4 transform -translate-x-16;
}

.solutions {
  @apply flex gap-20 my-40 items-center;
}
.solutions__col-right {
  @apply h-full w-1/2 grid grid-cols-6 grid-rows-6 gap-4 overflow-hidden;
}
.solutions__col-left {
  @apply w-1/2;
}
.solutions-img--1 {
  @apply col-start-1 col-span-6 row-span-full;
}
.solutions-img--2 {
  @apply col-start-1 col-span-6 row-span-full transform translate-x-full mx-4;
}

.about-us {
  @apply flex flex-col justify-center my-40;
}
.about-us__heading {
  @apply text-center flex flex-col items-center w-1/2 mx-auto;
}

.features {
  @apply flex gap-4 items-center mt-16;
}

.feature {
  @apply flex flex-col gap-4 justify-center items-center text-center;
}
.feature__title {
  @apply text-lg font-semibold;
}
.feature__body {
  @apply xl:w-4/5;
}

.gallery {
  @apply flex gap-20 my-40 items-center;
}
.gallery__col-left {
  @apply grow w-1/2 self-end;
}
.gallery__col-right {
  @apply grow w-1/2 grid place-items-center;
}
.gallery__group {
  @apply grid grid-cols-9 grid-rows-6 gap-4 mt-10;
}
.gallery__img--1 {
  @apply col-start-1 col-span-4 row-start-1 row-span-3;
}
.gallery__img--2 {
  @apply col-start-4 col-span-6 row-start-1 row-span-3 mx-10;
}
.gallery__img--3 {
  @apply row-start-4 row-span-3 col-start-1 col-span-6;
}
.gallery__img--4 {
  @apply row-start-4 row-span-3 col-start-6 col-span-4 mx-12 2xl:mx-4;
}

.become-member {
  @apply flex gap-20 my-40 items-center;
}
.become-member__col-left {
  @apply h-full w-1/2 self-end;
}
.become-member__col-right {
  @apply grow w-1/2 grid grid-cols-6 grid-rows-6 place-items-center;
}
.become-member__btns {
  @apply flex gap-10;
}
.become-member__img--1 {
  @apply col-start-1 col-span-5 row-span-full;
}
.become-member__img--2 {
  @apply col-start-1 col-span-5 row-span-full transform translate-x-8 translate-y-4;
}

.footer {
  @apply relative;
}
.footer::before {
  content: "";
  @apply absolute inset-0;
  background: linear-gradient(180deg, rgba(253, 91, 47, 0) 0%, #fd5b2f 100%);
  opacity: 0.1;
}
.footer .footer__nav {
  @apply container mx-auto flex gap-x-10 py-16 items-center;
}
.footer__company-info {
  @apply basis-1/4 flex flex-col gap-y-8;
}
.footer__logo {
  @apply text-3xl;
}
.footer__list {
  @apply flex gap-8 grow w-full justify-start;
}
.footer__list-group {
  @apply grow flex flex-col place-items-center gap-y-2 text-left;
}
.footer__list--title {
  @apply text-lg text-orange-red;
}
.footer__item {
  @apply text-left;
}
.footer__social-links {
  @apply flex gap-x-4 items-center;
}
.footer__social-link {
  @apply bg-white p-2 shadow-md w-10 h-10 grid place-items-center rounded-full;
}

:root {
  --font-body: Arial, sans-serif;
  --bg-blur: 384.04px;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --backdrop: hsla(0, 0%, 100%, 0.9);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
}

main {
  @apply my-40 container mx-auto;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.heading-title {
  @apply text-5xl w-4/5;
}
.heading-section {
  @apply text-base uppercase text-orange-red;
}
.body-text-1 {
  @apply text-base leading-loose my-12;
}
.body-text-2 {
  @apply text-sm leading-loose;
}

.search-form {
  @apply flex place-items-center h-24 p-4 w-full backdrop-blur-2xl rounded-md;
  background: rgba(243, 243, 243, 0.6);
}
.search-input__icon {
  @apply p-4;
}
.search-input__city {
  @apply bg-transparent grow;
}
.search-input__location {
  @apply bg-transparent grow;
}
.search-btn {
  @apply shrink-0;
}

input::placeholder {
  @apply text-lg;
  color: #989898;
}

.btn {
  @apply text-white py-3.5 px-7 border-none 
             rounded-md text-base font-medium;
}
.btn-icon-box {
  @apply w-8 h-8 border border-red-400 
                rounded-full grid place-items-center;
}
.btn--primary {
  @apply bg-orange-red;
}
.btn--secondary {
  @apply border border-solid border-orange-red text-orange-red;
}
.btn--tertiary {
  @apply flex items-center gap-4 text-orange-red;
}